.Itens-container {
	background-color: #444;
	margin: 8px 0;
	padding: 10px;
	display: flex;
	border-radius: 5px;
	justify-content: space-between;
	color: #eee;
	align-items: center;
	width: 100%;
}

.Itens-title {
	cursor: pointer;
}

.buttons-container {
	display: flex;
	justify-content: flex-end;
}

.Item-button{
	background-color: transparent;
	border:none;
	color: white;
	cursor: pointer;
	padding: 10px;
	border-radius:5px
}

.Item-button:hover{
	background-color: #a191d6;
}

.Badge{
	height: 20px;
	margin: auto;
	margin-right: 10px;
}

.Lista-pedidos{
	max-height: 500px;
	overflow-x: hidden;
	overflow-y: auto;
	padding-right: 5px;
	margin-top: 20px;
	border-top: solid 1px rgba(0, 0, 0, .25);
}

::-webkit-scrollbar {
width: 10px;

}

::-webkit-scrollbar-track {
	background: rgba(0, 0, 0, .25);
}

::-webkit-scrollbar-thumb {
background: #888;
}

::-webkit-scrollbar-thumb:hover {
background: #555;
}